import React, { useState, useEffect, useCallback } from 'react';
import { AppBar, Toolbar, Button, Box, IconButton, Drawer, List, ListItem, ListItemText } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import useMediaQuery from '@mui/material/useMediaQuery'; //    
import logo from '../assets/logo.png'; //  

const Navbar = () => {
  const [showNavbar, setShowNavbar] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);
  const [drawerOpen, setDrawerOpen] = useState(false); //   b
  const isMobile = useMediaQuery('(max-width: 600px)'); // ,     600px

  //   handleScroll  useCallback,   
  const handleScroll = useCallback(() => {
    //       
    if (!isMobile) {
      const currentScrollY = window.scrollY;

      if (currentScrollY > lastScrollY) {
        setShowNavbar(false);
      } else {
        setShowNavbar(true);
      }

      setLastScrollY(currentScrollY);
    }
  }, [lastScrollY, isMobile]);

  useEffect(() => {
    if (!isMobile) {
      window.addEventListener('scroll', handleScroll);
    }

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [handleScroll, isMobile]);

  const handleMouseEnter = () => {
    if (!showNavbar) {
      setShowNavbar(true);
    }
  };

  const toggleDrawer = (open) => () => {
    setDrawerOpen(open);
  };

  const menuItems = [
    { text: ' ', link: '#about-robot' },
    { text: ' ', link: '#how-to-start' },
    { text: '', link: '#advantages' },
    { text: '', link: '#community' },
  ];

  return (
    <>
      <AppBar
        position="fixed"
        sx={{ backgroundColor: '#e4e2dd' }} //      
        className={`navbar ${showNavbar ? 'navbar-show' : 'navbar-hide'}`}
        onMouseEnter={handleMouseEnter}
      >
        <Toolbar>
          {/*  */}
          <Box sx={{ flexGrow: 1 }}>
            <img src={logo} alt="" style={{ height: '50px' }} />
          </Box>

          {/*     */}
          {!isMobile && (
            <>
              <Button color="inherit" href="#about-robot"> </Button>
              <Button color="inherit" href="#how-to-start"> </Button>
              <Button color="inherit" href="#advantages"></Button>
              <Button color="inherit" href="#community"></Button>
            </>
          )}

          {/*     */}
          {isMobile && (
            <IconButton
              edge="end"
              color="inherit"
              aria-label="menu"
              onClick={toggleDrawer(true)}
            >
              <MenuIcon />
            </IconButton>
          )}
        </Toolbar>
      </AppBar>

      {/*      */}
      <Drawer
        anchor="right"
        open={drawerOpen}
        onClose={toggleDrawer(false)}
      >
        <List>
          {menuItems.map((item, index) => (
            <ListItem button key={index} component="a" href={item.link}>
              <ListItemText primary={item.text} />
            </ListItem>
          ))}
        </List>
      </Drawer>
    </>
  );
};

export default Navbar;
